* {
  margin: 0;
  padding: 0;
  /* border: 2px solid black; */
}

.header {
  display: flex;
  justify-content: center;
  font-size: 4rem;
  font-weight: 100;
  background-color: black;
  color: white;
}

.container {
  width: 100%;
  min-height: 100vh;
  background: url("background5.png");
  background-repeat: repeat-y;
  background-size: cover;
  border-radius: 10px;
}

.title {
  width: 70%;
  height: 60px;
  display: flex;
  margin: auto;
}

.title > input {
  width: 100%;
  margin-top: 10px;
  border: none;
  outline: none;
  padding: 20px;
  border-radius: 10px 0px 0px 10px;
}

.title > button {
  margin-top: 10px;
  width: 80px;
  background-color: black;
  color: white;
  cursor: pointer;
  border-radius: 0px 10px 10px 0px;
}

.title > button:hover {
  background-color: rgba(0, 0, 0, 0.466);
}

.task {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 60%;
  background: white;
  margin: 20px auto;
  padding: 15px;
  border-radius: 10px;
}

.task button {
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  cursor: pointer;
  height: 50px;
  border-radius: 10px;
  color: white;
  background: black;
}

.task button:hover {
  background: rgba(0, 0, 0, 0.454);
}

.buttonDiv {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.taskTitle {
  width: 100%;
}
.deleteBtn {
  background-color: red;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.deleteBtn:hover {
  background-color: rgba(255, 0, 0, 0.502);
}
.icon {
  width: 25px;
  height: 30px;
  cursor: pointer;
  color: red;
  margin: 0px 20px;
}
.icon:hover {
  color: rgba(255, 0, 0, 0.502);
}

.view {
  display: flex;
  flex-direction: column;
  background: url("background5.png");
  background-repeat: no-repeat;
  background-size: contain;
  min-height: 100vh;
}

.view button {
  width: 50%;
  height: 60px;
  margin: 100px 350px;
  cursor: pointer;
  color: white;
  background: black;
  font-size: 30px;
  border-radius: 20px;
  outline: none;
  border: none;
}
.view button:hover {
  background-color: rgba(0, 0, 0, 0.625);
}

.viewCards {
  display: flex;
  margin-top: 40px;
}

.cards {
  width: 200px;
  height: 100px;
  /* background-color: rgba(255, 0, 0, 0.504); */
  background-color: black;
  color: white;
  margin: 10px;
  text-align: center;
  padding: 30px;
  font-size: 25px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}

.cardStatus {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.status {
  display: flex;
  flex-direction: column;
}

.numberTasks {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.toggle {
  margin: 0px 20px;
}
